var _self;

var api = require('../../common/api.js')
export default {

	onLoad() {
		_self = this;

	},

	methods: {

		exit() {
			uni.reLaunch({
				url: "../home/index"
			});
		},

		getUserInfoFun: function(e) {
			console.log(e.detail.userInfo);

			if (e.detail.userInfo) {
				api.userInfo = e.detail.userInfo;
				uni.navigateBack();				

				//用户按了允许授权按钮
			} else {
				return false

			}
		},

	}




}
